@font-face {
    font-family: "ZenMaruGothicRegular";
    src: url("./fonts/ZenMaruGothic-Regular.ttf") format("truetype");
}

@font-face {
    font-family: "ZenMaruGothicMedium";
    src: url("./fonts/ZenMaruGothic-Medium.ttf") format("truetype");
}

:root {
    --regular-font: "ZenMaruGothicRegular";
    --medium-font: "ZenMaruGothicMedium";
    --font-color: "black";
}

html, body {
    background-color: aliceblue;
}

*:not(.title) {
    font-family: var(--regular-font), sans-serif;
    color: var(--font-color);
    font-size: x-large;
}

.elem {
    padding: 0.2em;
    margin: 0.2em;
}

.title {
    font-family: var(--medium-font), sans-serif;
    color: var(--font-color);
    font-size: 30px;
    font-weight: bold;
}

.smalltext {
    font-size: large;
}

.center {
    display: block;
    text-align: center;
    margin-right: auto;
    margin-left: auto;
}

.parentBox {
    display: flex;
}

.parentBox > .box {
    width: 50%;
}

.box {
    padding: 10px;
    margin: 20px;
    border-radius: 20px;
}

.line {
    border: 3px black solid;
}

.input {
    border: 1.5px darkgray solid;
    border-radius: 10px;
    width: 10em;
    max-width: 98%;
}

.button {
    width: 200px;
    border: 5px black solid;
    border-radius: 10px;
    background-color: aquamarine;
    max-width: 95%;
}

.button:hover {
    border-color: darkgray;
}

select.input {
    overflow-y: scroll;
}

input[type="checkbox"] {
  appearance: auto;
  -webkit-appearance: checkbox;
  -moz-appearance: checkbox;

  width: 16px;
  height: 16px;

  margin: 0;
}

.space {
    margin-right: 5px;
    margin-left: 5px;
}

table.table {
    border-collapse: collapse;
    width: 100%;
    table-layout: fixed;
}

th, td {
    border: 3px solid black;
    padding: 8px 12px;
    vertical-align: middle;
}

th {
    font-size: xx-large;
}

